<template>
  <div name="hello" class="container">
    <router-view class="content"></router-view>
    <div class="row">
      <div class="nav nav-pills" style="width:100%">
        <div class="btn-group btn-group-lg" style="width:100%" role="group">
  
          <button type="button" class="btn btn-default col-xs-6" v-for="item in routes" :key="item.key" v-on:click="goto(item.key)">{{item.value}}</button>
        </div>
      </div>
    </div>
    <!--<div v-for="item in routes" class="nav-item" v-on:click="goto(item)">{{item}}</div>-->
  </div>
</template>

<script>
import router from '../router/index';
export default {
  name: 'hello',
  data() {
    return {
      routes: [{ key: 'MyCharts', value: 'Charts' }, { key: 'MyRecord', value: 'Record' }, { key: 'Log', value: 'Log' }, { key: 'Mycal', value: 'CalTool' }]
    };
  },
  methods: {
    goto: function (routeName) {
      router.push(routeName);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.content {
  position: absolute;
  top: 60px;
  left: 0px;
  right: 0px;
  bottom: 92px;
}

div>button {
  font-weight: bold;
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
}

div>button:hover {
  color: crimson;
}

div>button:active {
  color: crimson;
}

div.nav {
  position: absolute;
  bottom: 0px;
  left: 0px;
  background-image: url(../assets/images/navitem.jpg);
  background-size: 100% 100%;
  background-repeat: no-repeat;
}


.nav-item {
  display: inline-block;
  width: 200px;
  height: 200px;
  margin: 16px 10px;
  text-align: center;
  line-height: 60px;
  color: #fff;
  font-size: 2rem;
  border: 2px solid #000;
  border-radius: 10px;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.5);
  background-image: url(../assets/images/navitem.jpg);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  transition: 1s;
  -moz-transition: 1s;
  -webkit-transition: 1s;
}

.nav-item:hover {
  width: 300px;
  height: 300px;
}

@media (max-height:400px) {
  div.nav {
    display: none;
  }
  div.content {
    bottom: 0px;
  }
}
</style>



