<template>
    <div name="mycal" class="mycal">
        <form class="form-inline">
            <div class="form-group col-xs-6 col-md-6 left edit-block" v-for="item in model" :key="item.title">
                <label>{{item.title}}</label>
                <input type="number" class="form-control" :placeholder="item.remark" aria-describedby="basic-addon1" v-model="item.value">
            </div>
        </form>
        <div>
            <div>
                <p>五场差距</p>
                <span class="record_input3 col-xs-6">{{left1}}</span>
                <span class="record_input3 col-xs-6">{{right1}}</span>
            </div>
            <div>
                <p>十场差距</p>
                <span class="record_input3 col-xs-6">{{left2}}</span>
                <span class="record_input3 col-xs-6">{{right2}}</span>
            </div>
            <div>
                <span class="record_input3 col-xs-6">{{stateChange}}</span>
                <span class="record_input3 col-xs-6">{{stateChange2}}</span>
            </div>
        </div>
        <div class="btn-group btn-group-lg">
            <button class="btn btn-default" v-on:click='calculate'>Cal</button>
            <button class="btn btn-default" v-on:click='resetStore'>{{message}}</button>
        </div>
    </div>
</template>
<script>
import Store from '../services/store';
import CalUtil from '../services/cal';
export default {
  name: 'mycal',
  data() {
    return {
      model: {
        hRate: { title: 'Home Status', remark: '主队状态', value: '' },
        gRate: { title: 'Guest Status', remark: '客队状态', value: '' },
        hs: { title: 'HS5', remark: '主队5场进球', value: '' },
        gs: { title: 'GS5', remark: '客队5场进球', value: '' },
        hn: { title: 'HN5', remark: '主队5场被进球', value: '' },
        gn: { title: 'GN5', remark: '客队5场被进球', value: '' },
        hs2: { title: 'HS10', remark: '主队10场进球', value: '' },
        gs2: { title: 'GS10', remark: '客队10场进球', value: '' },
        hn2: { title: 'HN10', remark: '主队10场被进球', value: '' },
        gn2: { title: 'GN10', remark: '客队10场被进球', value: '' }
      },
      left1: '',
      left2: '',
      right1: '',
      right2: '',
      message: 'Winner!',
      modelList: Store.fetch().length > 0 ? Store.fetch() : [],
      teamName: ''
    };
  },
  methods: {
    calculate: function() {
      // 转为数字
      for (let item in this.model) {
        this.model[item].value = parseFloat(this.model[item].value);
      }
      // this.stateChange = `进攻:${this.model.hs - this.model.hs2},防守：${this.model
      //   .hn - this.model.hn2}`;
      // this.stateChange2 = `进攻:${this.model.gs - this.model.gs2},防守：${this.model
      //   .gn - this.model.gn2}`;
      this.left1 = parseFloat(
        (CalUtil.sortRate(this.model.hs.value, this.model.gn.value) *
          this.model.hRate.value
        ).toFixed(2)
      );
      this.left2 = parseFloat(
        (CalUtil.sortRate(this.model.hs2.value, this.model.gn2.value) * 1
        ).toFixed(2)
      );
      this.right1 = parseFloat(
        (CalUtil.sortRate(this.model.gs.value, this.model.hn.value) *
          this.model.gRate.value
        ).toFixed(2)
      );
      this.right2 = parseFloat(
        (CalUtil.sortRate(this.model.gs2.value, this.model.hn2.value) * 1
        ).toFixed(2)
      );
      this.message = ((this.left1 + this.left2 - (this.right1 + this.right2)) /
        2
      ).toFixed(2);
      this.teamName = window.prompt('请输入队名！');
      if (this.teamName) {
        this.addToList();
      }
      this.model = {
        hRate: { title: 'Home Status', remark: '主队状态', value: '' },
        gRate: { title: 'Guest Status', remark: '客队状态', value: '' },
        hs: { title: 'HS5', remark: '主队5场进球', value: '' },
        gs: { title: 'GS5', remark: '客队5场进球', value: '' },
        hn: { title: 'HN5', remark: '主队5场被进球', value: '' },
        gn: { title: 'GN5', remark: '客队5场被进球', value: '' },
        hs2: { title: 'HS10', remark: '主队10场进球', value: '' },
        gs2: { title: 'GS10', remark: '客队10场进球', value: '' },
        hn2: { title: 'HN10', remark: '主队10场被进球', value: '' },
        gn2: { title: 'GN10', remark: '客队10场被进球', value: '' }
      };
    },
    addToList: function() {
      this.modelList.unshift({
        l1: this.left1,
        l2: this.left2,
        r1: this.right1,
        r2: this.right2,
        teamName: this.teamName,
        message: this.message
      });
      Store.save(this.modelList);
    },
    resetStore: function() {
      if (window.confirm('确定清空储存？')) {
        Store.reset();
      }
    }
  },
  computed: {
    stateChange: function() {
      return `${(parseFloat(this.model.hs.value) -
        parseFloat(this.model.hs2.value)).toFixed(2)}/${(parseFloat(this.model.hn.value) -
        parseFloat(this.model.hn2.value)).toFixed(2)}`;
    },
    stateChange2: function() {
      return `${(parseFloat(this.model.gs.value) -
        parseFloat(this.model.gs2.value)).toFixed(2)}/${(parseFloat(this.model.gn.value) -
        parseFloat(this.model.gn2.value)).toFixed(2)}`;
    }
  }
};
</script>
<style lang="scss" scoped>
.mycal {
  overflow-y: auto;
  padding-bottom: 10px;
}

input {
  text-align: center;
}

.left:nth-child(2n + 1) {
  border-right: 1px solid cornflowerblue;
}

.btn-default {
  width: 110px;
  color: #333;
  background-color: #fff;
  border-color: #ccc;
}

.record_input {
  width: 100%;
  height: 30px;
  font-size: 1rem;
  text-align: center;
  line-height: 20px;
  border: 5px groove;
}

.record_input3 {
  height: 30px;
  font-size: 1rem;
  text-align: center;
  line-height: 20px;
  border: 5px groove cornflowerblue;
  margin-bottom: 5px;
  border-radius: 10px;
}

.record_list_model:nth-child(2n + 1) input {
  border: 5px groove #289ed4;
}

.add_record_btn {
  border-top: 2px solid;
  padding-top: 5px;
}

.record_btn_style {
  width: 49%;
  float: left;
  text-decoration: none;
  background: indianred;
  margin-top: 2px;
  padding: 0;
  height: 40px;
  line-height: 40px;
  font-size: 1.8rem;
}
.edit-block {
  display: flex !important;
  height: 55px;
  justify-content: center;
  align-content: center;
  flex-direction: column;
}
</style>



